//Import Modules
import { Router, RouterModule, Routes } from '@angular/router';
import { NgModule } from '@angular/core';

// Import Components
import { AppComponent } from './app.component';
import { HomeComponent } from './Home/home.component';
import { AiBannerComponent } from './Home/Ai Banner/aiBanner.component';
import { ContactFormComponent } from './Home/Contact Form/contactForm.component';
import { FeaturesComponent } from './Home/Features/features.component';
import { FooterComponent } from './Home/Footer/footer.component';
import { HowItWorksComponent } from './Home/How It Works/howItWorks.component';
import { ForUsersComponent } from './Home/How It Works/For Users/forUsers.component';
import { ForBrandsComponent } from './Home/How It Works/For Brands/forBrands.component';
import { MainBannerSliderComponent } from './Home/Main Banner Slider/mainBannerSlider.component';
import { NavigationBarComponent } from './Home/Navigation/navigationBar.component';
import { NewsletterSignUpComponent } from './Home/Newsletter Sign Up/newsletterSignUp.component';
import { WhySyzeComponent} from './Home/Why Syze/whySyze.component';
import { NotFoundComponent } from './Not Found/notFound.component';

const routes: Routes = [
    { path: '', redirectTo: '/users', pathMatch: 'full' },
    { path: 'users', component: ForUsersComponent },
    { path: 'brands', component: ForBrandsComponent },
    { path: '**', component: NotFoundComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
  providers: []
})
export class RoutingModule { }


