import { Component } from '@angular/core';

@Component({
    selector: 'custom-navbar',
    templateUrl: './navigationBar.template.html',
    styleUrls: ['./navigationBar.styles.css']
})
export class NavigationBarComponent {
    constructor() {}

    yCoord = {
        'home' : 0,
        'howWork' : 503,
        'features' : 1144,
        'whySyze' : 2244,
        'contact' : 2778
    }

    ngOnInit() {
        // console.log(window.scrollY);
    }

    goToHome() {
        window.scrollTo(0, this.yCoord.home);
    }

    goToWork() {
        window.scrollTo(0, this.yCoord.howWork);
    }

    goToFeatures() {
        window.scrollTo(0, this.yCoord.features);
        // console.log("Features!")
    }

    goToWhy() {
        window.scrollTo(0, this.yCoord.whySyze);
    }

    goToContact() {
        window.scrollTo(0, this.yCoord.contact);
    }
}


